.box {
    border: 1px solid gray;
    border-radius: 5px;
    width: 640px;
    margin: 0 auto;
    padding: 15px;
}

#login-form div {
    padding: 5px;
}

#login-form h2 {
    margin: 0 0 15px 0;
}

@media only screen and (max-width: 700px) {
/*     body {
